module.exports = {
    title: 'Text',
    scroll: true,
    examples: [
        {
            subtitle: 'Text',
            render: function() {
                return (
                    <View style={{backgroundColor: '#ffffff', padding: 5}}>
                        <Text>
                             这就是一段普通的文字。
                        </Text>
                    </View>
                );
            },
        },
        {
            subtitle: 'Iconfont',
            render: function() {
                return (
                    <View style={{backgroundColor: '#ffffff', padding: 5}}>
                        <View><Text>以下是qrn内置的字体的内容，字体名为 qunar_react_native 。</Text></View>
                        <View style={{padding: 15}}>
                            {FONTLIST.map((item, index) =>
                                <Text key={index} style={{fontFamily:'qunar_react_native', fontSize: 14, height: 40}}>{item.value}  {item.name}</Text>
                            )}
                        </View>
                    </View>
                );
            },
        },
    ]
};



