class NetworkImageCallbackExample extends Component {
    constructor() {
        super();
        this._loadEventFired = this._loadEventFired.bind(this);
        this.state = {
            events: [],
            mountTime: new Date(),
        };
    }

    componentWillMount() {
        this.setState({mountTime: new Date()});
    }

    render() {
        var { mountTime } = this.state;

        return (
            <View>
                <Image
                    source={this.props.source}
                    style={[styles.base, {overflow: 'visible'}]}
                    onLoadStart={() => this._loadEventFired(`✔ onLoadStart (+${new Date() - mountTime}ms)`)}
                    onLoad={() => this._loadEventFired(`✔ onLoad (+${new Date() - mountTime}ms)`)}
                    onLoadEnd={() => this._loadEventFired(`✔ onLoadEnd (+${new Date() - mountTime}ms)`)}
                />

                <Text style={{marginTop: 20}}>
                    {this.state.events.join('\n')}
                </Text>
            </View>
        );
    }

    _loadEventFired(event) {
        this.setState((state) => {
            return state.events = [...state.events, event];
        });
    }
}

class NetworkImageExample extends Component {
    constructor() {
        super();
        this.state = {
            error: false,
            loading: false,
            progress: 0
        };
    }

    render() {
        var loader = this.state.loading ?
                     <View style={styles.progress}>
                         <Text>{this.state.progress}%</Text>
                         <ActivityIndicatorIOS style={{marginLeft:5}}/>
                     </View> : null;
        return this.state.error ?
               <Text>{this.state.error}</Text> :
               <Image
                   source={this.props.source}
                   style={[styles.base, {overflow: 'visible'}]}
                   onLoadStart={(e) => this.setState({loading: true})}
                   onError={(e) => this.setState({error: e.nativeEvent.error, loading: false})}
                   onProgress={(e) => this.setState({progress: Math.round(100 * e.nativeEvent.loaded / e.nativeEvent.total)})}
                   onLoad={() => this.setState({loading: false, error: false})}>
                   {loader}
               </Image>;
    }
}

class ImageSizeExample extends Component {
    constructor() {
        super();
        this.state = {
            width: 0,
            height: 0,
        };
    }

    componentDidMount() {
        Image.getSize(this.props.source.uri, (width, height) => {
          this.setState({width, height});
        });
    }

    render() {
        return (
            <View style={{flexDirection: 'row'}}>
                <Image
                    style={{
                            width: 60,
                            height: 60,
                            backgroundColor: 'transparent',
                            marginRight: 10,
                        }}
                    source={this.props.source} />
                <Text>
                    Actual dimensions:{'\n'}
                    Width: {this.state.width}, Height: {this.state.height}
                </Text>
            </View>
        );
    }
}

